import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import { Button } from './Button';
import './navBar.css'; 

function NavBar() {  // component name should be capital 

    const [click, setClick] = useState(false);
    const [buttton, setButton] = useState(true);

    const CloseMobileMenu = () => setClick(false)

    const showButton =() => {

        if (window.innerWidth <=960) setButton(false)
        else setButton(true)
    }
    
    useEffect( showButton , []); // run it one time

    window.addEventListener('resize', showButton);


    return (
        <div>
            <nav className="navbar">
                <div className="navbar-container">
                    <Link to="/" className="navbar-logo" onClick={CloseMobileMenu}> TRVL </Link>
                    <div className="menu-icon"  
                        onClick={() => {
                                setClick (!click);
                                console.log(click);
                            }}>
                        <i class={click ? "fas fa-times" : "fas fa-bars"}></i>
                    </div>
                    <ul className={click ? "nav-menu active" : "nav-menu"}>
                        <li className="nav-item"> 
                            <Link to="/" className="nav-links" onClick={CloseMobileMenu}> Home </Link> 
                        </li>
                        <li className="nav-item"> 
                        <Link to="/services" className="nav-links" onClick={CloseMobileMenu}> Services </Link> 
                        </li>
                        <li className="nav-item"> 
                            <Link to="/products" className="nav-links" onClick={CloseMobileMenu}> Products </Link> 
                        </li>
                        
                        <li className="nav-item"> 
                            <Link to="/signup" className="nav-links-mobile" onClick={CloseMobileMenu}> Sign Up </Link> 
                        </li>
              
                    </ul>

                    {buttton && <Button buttonStyle="btn--outline" childern="SIGN UP"/>}  
                </div>
            </nav>
        </div>
    )
}
//{buttton && <Button buttonStyle='btn--outline' > SIGN UP</Button> }  // && means return 

export default NavBar;